body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.adHeading{
    text-align: center;
}
.msgDisplay{
    margin-top: 2%;
}
.navCustom{
    position: -webkit-sticky;
    position: sticky;
}
.displayTxt{
    text-align: center;
    /* font-weight: bold; */
    font-size: 80%;
}
.paragraph{
    
    background-image:linear-gradient(rgba(26, 26, 26, 0.7),rgba(26, 26, 26, 0.7)), url(/static/media/img3.dba23448.jpg);
    background-size: cover;

}
.quickLinks{
    text-align: center;
    font-weight: bold;
    font-size: 150%;
}
.welcomeAdmin{
    background-color: rgba(26, 26, 26, 0.7);
    font-weight: bolder;
    font-size: 300%;
    color: white;
    text-align: center;
}
.newsFeed{
    height: 80%;
}


/*Custom Css Starts Here*/
.ctmHeading{
    font-weight: bold;
    font-size: 150%;
}
.ctmHeadingCenter{
    text-align: center;
    font-weight: bold;
    font-size: 150%;
}
.ctmClrWhite{
    color: white;
}
.ctmClrBlack{
    color: black;
}
.ctmClrRed{
    color: red;
}
.ctmBgWhite{
    background-color: white;
}
.ctmBgTeal{
    background-color: lightslategrey;
}
.ctmBold{
    font-weight: bold;
}
.ctmBtnFontX{
    font-weight: bolder;
}
.ctmBorderRight{
    border-right: 4px double white;
}
.ctmBorderBottom{
    border-bottom: 4px double black;
}
.ctmBorderDbBottomWhite{
    border-bottom: 4px double white;
}
.ctmStatusBoxBgWhiteFcBlack{
    margin: 1em;
    background-color: rgba(255, 255, 255);
    border-radius: 30px;
    border: 0.5em double whitesmoke;
}
.ctmStatusTitle{
    padding: 0.4em;
    text-align: center;
    font-size: 1.5em;
    font-weight: bolder;
    border-bottom: 0.2em double black;
}
.ctmStatusTagLine{
    padding: 0.2em;
    font-size: 1em;
    font-weight: bolder;
}
.ctmStatusTagLineCenter{
    padding: 0.2em;
    text-align: center;
    font-size: 1em;
    font-weight: bolder;
}
.ctmStatusValue{
    margin: 0.2em;
    text-align: center;
    font-size: 1em;
    font-weight: bolder;
}
.ctmRowMargin{
    margin-top: 2em;
}
.ctmBorderBU{
    border-bottom: 1em dashed red;
    border-top: 1em dashed red;
}


.footerList{
    border-radius: 5%;
}
.ctmPaddingX{
    padding: 2em;
}
.customBg{
    background-color: rgba(0, 0, 0, 0.8);
}
.contactBg{
    background-color: teal;
    padding: 3%;
}
.contactFormHeading{
    font-size: 200%;
    font-weight: bold;
}
.customBgFrom{
    background-color: #fff;
    margin-top: 25px;
    padding:20px;
    border-radius: 5%;
}
.displayLinks{
    /*for footer links*/
    font-size: 70%;
    text-align: center;
    color: black;
}

